.page-footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-top: 18px;
    padding-bottom: 13px;
}

.page-footer__hash {
    font-size: 21px;
    line-height: 26px;
    font-weight: bold;
    text-transform: uppercase;
}

.page-footer__social {
}

.page-footer__copyright {
}

@media (max-width: $mobile-width-only) {
    .page-footer {
        flex-direction: column;
    }   
}

@media (min-width: $tablet-width) {
    .page-footer {
        padding-top: 26px;
        padding-bottom: 26px;
    }
}